<template>
    <div>
        <breadcrumb-box>
         </breadcrumb-box>
         <el-card >
             <div class="articleBox">
                <tip :txt="name =='DataList'?'暂无资料':'暂无文章'" oder="3" v-if="isShow"></tip>
                <div style="margin-top:10px" >
                    <el-row :gutter="20">
                        <el-col :span="8" style="margin-bottom:20px" v-for="(item,index) in list" :key="index">
                            <el-card shadow='hover' body-style="padding:0">
                                <router-link v-if="name =='DataList'" :to="'/dataDetail/'+item.id+'?fromPage=DataList'">
                                    <div style="margin:20px">
                                        <h3 class="article-title ellipsis">
                                                {{item.title}}
                                        </h3>
                                        <div class="time">{{item.mtime}}更新</div>
                                        <div class="detail">
                                            <span v-html="item.brief"></span>
                                        </div>
                                        <div class="relatedTopic">
                                            {{item.subject_title}}
                                        </div>
                                    </div>
                                </router-link>
                                <router-link v-if="name =='ArticleList'" :to="'/articleDetail/'+item.id+'?fromPage=ArticleList'">
                                    <div style="margin:20px">
                                        <h3 class="article-title ellipsis" >
                                                {{item.title}}
                                        </h3>
                                        <div class="time">{{item.mtime}}更新</div>
                                        <div class="detail">
                                            <span v-html="item.brief"></span>
                                        </div>
                                        <div class="relatedTopic">
                                            {{item.subject_title}}
                                        </div>
                                    </div>
                                </router-link>

                                <div class="flexBox" style="align-items：center;text-align:center">
                                    <div class="flexItem">
                                        <i @click="edit(item.id)" class="el-icon-edit-outline" title="编辑"
                                        style="font-size:20px;cursor:pointer"></i>
                                    </div>
                                    <div class="flexItem">
                                        <i @click="delDialog(item.id)" class="el-icon-delete" title="删除"
                                        style="font-size:20px;cursor:pointer"></i>
                                    </div>
                                </div>
                            </el-card>
                        </el-col>
                    </el-row>
                    <div class="loadMore">
                    <span class="more" v-if="!(total==list.length)" @click="loadMore">加载更多</span>
                    <span v-if="(total==list.length && page>2)">数据加载完了</span>
                    </div>
                </div>
             </div>

        </el-card>
        <el-dialog
        center
        :visible.sync="dialogVisible"
        width="300px">
        <div style="text-align:center;color:red">确定要删除？删除后将不能恢复</div>
        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="del">确 定</el-button>
        </span>
        </el-dialog>
    </div>
</template>
<script>
//import Avatar from '@/components/Avatar'

import BreadcrumbBox from '@/layout/BreadcrumbBox'
import Tip from '@/components/Tip/index'
import { mapState } from '@icony/vue-container/vuex'
export default {
    name: 'ArticleList',
    components: {
        BreadcrumbBox,
        Tip
    },
    data() {
        return {
            name:'',
            dialogVisible: false,
            id: '',
            page: 1,
            total: 0,
            list: [],
            flag: true,
            isShow:false
        }
    },
    mounted(){
        const name = this.$route.name
        this.name = name
        this.findList({page: 1,limit: 9})
    },
    methods: {
        findList(data){
            if(this.name == 'ArticleList'){
                this.articleMineList(data)
            }else{
                this.materialMineList(data)
            }
        },
      articleMineList(data,from){
          this.$store.dispatch('article/articleMineList',data).then((result)=>{
              const {total,list} = result.data
              if(from == 'del'){
                this.total = total
                this.list = list
                this.isShow = list.length<1?true:false
              }else{

                const mergeList = this.list.concat(list);
                if(this.list.length<total){
                    this.flag = true
                }else{
                    this.flag = false
                }
                this.page = ++ this.page
                this.total = total
                this.list = mergeList
                this.isShow = mergeList.length<1?true:false
              }

            })

        },
        materialMineList(data,from){
           this.$store.dispatch('article/materialMineList',data).then((result)=>{
              const {total,list} = result.data
              if(from == 'del'){
                this.total = total
                this.list = list
                this.isShow = list.length<1?true:false
              }else{
                const mergeList = this.list.concat(list);
                if(this.list.length<total){
                    this.flag = true
                }else{
                    this.flag = false
                }
                this.page = ++ this.page
                this.total = total
                this.list = mergeList
                this.isShow = mergeList.length<1?true:false
              }
            })

        },
        delDialog(id){
            this.dialogVisible = true
            this.id = id
        },
        filterList(id){
           const list = this.list;
           const newList =  list.filter((item)=>{
               return item.id != id
            })
           this.list = newList
        },
        del(){
            const id = this.id;
            if(this.name == 'ArticleList'){
                this.$store.dispatch('article/articleRemove',{id}).then(()=>{
                    this.articleMineList({page: 1,limit: 9},'del')
                    this.dialogVisible = false
                    this.page = 1
                })
             }else{
                this.$store.dispatch('article/materialRemove',{id}).then(()=>{
                    this.materialMineList({page: 1,limit: 9},'del')
                    this.dialogVisible = false
                    this.page = 1
                })
             }
        },
        edit(id){
             if(this.name == 'ArticleList'){
                 this.$router.push(`/editArticle/${id}`)
             }else{
                 this.$router.push(`/editData/${id}`)
             }
        },
        loadMore(){
            if(this.flag){
                this.findList({page: this.page,limit: 9})
            }

        }
    },
}
</script>
<style lang="scss" scoped>
 @import "~@/styles/variables.scss";
.articleBox {
    min-height: 400px;
}
 .article-title {
     margin-top:0;
     margin-bottom: 10px;
 }

 .time {
     font-size: 12px;
     color: $placeholderText;
 }

 .loadMore {
     text-align: center;
     padding: 10px 0;
     .more {
         cursor: pointer;
         font-size: 16px;
         &:hover{
             color: #000
         }
     }
 }

 .detail {
     font-size: 12px;
     color: $secondaryText;
     margin-top:10px;
     line-height: 1.6;
     height: 40px;
     display:-webkit-box;
     overflow:hidden;
     text-overflow:ellipsis;
     -webkit-line-clamp:2;
     -webkit-box-orient:vertical;
     margin-bottom: 6px;
     word-break: break-all;
 }

 .relatedTopic {
     padding:8px 10px;
     background: #f2f2f2;
     border-radius: 2px;
     font-size: 12px;
     color: $secondaryText;
 }

 .flexBox {
     margin-top: 10px;
     border-top: 1px solid $border-2;
     padding: 10px
 }
 .articleBox /deep/ .el-card{
   border: 2px solid #EBEEF5;
 }


 .el-card.is-hover-shadow:focus, .el-card.is-hover-shadow:hover{
   border: 2px solid rgba(64,158,255,.2) ;

   -webkit-box-shadow: 0 2px 12px 0 rgba(64,158,255,.4);
   box-shadow: 0 2px 12px 0 rgba(64,158,255,.4);


 }
</style>
